<template>
  <div>
    <div class="main-title">
      Categories
      <CategoriesCreateEdit :docId="0" :name="''" @reloadCategoriesList="getCategoriesList" />
    </div>
    <div class="create-account-container" v-if="false">
      <CategoriesCreateEdit :docId="0" :name="''" @reloadCategoriesList="getCategoriesList" />
    </div>

    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(categoriesList)" :key="k" class="bank-account-element">
        <div class="bank-account-title">
          {{ categoriesList[k].name }}
          <CategoriesCreateEdit :docId="k" :name="categoriesList[k].name" @reloadCategoriesList="getCategoriesList" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CategoriesCreateEdit from "@/components/CategoriesCreateEdit.vue"
import { mapGetters, mapMutations } from 'vuex';
import { collection, query, getDocs } from "firebase/firestore";

export default {
  name: 'CategoriesList',
  components: {
    CategoriesCreateEdit,
  },
  data() {
    return {
      categoriesList: {},
    }
  },
  computed: {
    ...mapGetters(["db",]),
  },
  methods: {
    ...mapMutations(["setCategoriesList"]),
    async getCategoriesList() {
      const q = query(collection(this.db, "categories"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.categoriesList[doc.id] = doc.data();
      });

      this.setCategoriesList(this.categoriesList);
    }
  },
  mounted() {
    this.getCategoriesList();
  },
}
</script>

