<template>
<dialog class="modal-container" :id="id">
  <div class="close-btn-container">
    <div class="close-btn" @click="closeModal"></div>
  </div>
  <!-- <div class="modal-subtitle">Filters:</div> -->
  <div class="filter-elements-container">
    <div class="filter-item">
      <div class="label-with-btn-container">
        <label for="t-filter-order">Order by:</label>
        <div class="btn-in-label-container">
          <div class="btn-in-label-text">Desc</div>
          <div class="btn-in-label-moving-btn" :class="[transictionOrderDesc ? 'btn-in-label-moving-btn-active' : '']" @click="transictionOrderDesc = !transictionOrderDesc"><div></div></div>
          <div class="btn-in-label-text">Asc</div>
        </div>
      </div>
      <select name="" id="t-filter-order" class="form-control" v-model="transactionOrderBy">
        <option value="date">Date</option>
        <option value="amount">Amount</option>
      </select>
    </div>
    <div class="filter-item">
      <label for="t-filter-type">Type:</label>
      <select id="t-filter-type" class="form-control" v-model="transactionType">
        <option value="both">Both</option>
        <option value="income">Incomes</option>
        <option value="expense">Expenses</option>
      </select>
    </div>
    <div class="filter-item">
      <label for="t-filter-category">Category:</label>
      <select name="" id="t-filter-category" class="form-control" v-model="transactionCategory">
        <option value="0">All</option>
        <option v-for="k in Object.keys(filteredCategoriesList)" :key="k" :value="k">{{ filteredCategoriesList[k].name }}</option>
      </select>
    </div>
    <div class="filter-item">
      <label for="t-start-date">From:</label>
      <input type="date" name="" id="t-start-date" class="form-control" v-model="transactionStartDate" />
    </div>
    <div class="filter-item">
      <label for="t-end-date">To:</label>
      <input type="date" name="" id="t-end-date" class="form-control" v-model="transactionEndDate" />
    </div>
  </div>
  <div class="create-btn" @click="applyFilter">Apply</div>
</dialog>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TransactionFilters',
  props: ["id"],
  data() {
    return {
      transactionOrderBy: null,
      transictionOrderDesc: null,
      transactionType: null,
      transactionCategory: null,
      transactionStartDate: null,
      transactionEndDate: null,
    }
  },
  computed: {
    ...mapGetters(["categoriesList"]),
    filteredCategoriesList() {
      if(this.transactionType == "both") {
        return this.categoriesList;
      }
      const result = {};
      Object.keys(this.categoriesList).forEach(x => {
        if(this.categoriesList[x].transactionType == this.transactionType) {
          result[x] = this.categoriesList[x];
        }
      });
      return result;
    },
  },
  methods: {
    setDefaultValues() {
      this.transactionOrderBy = "date";
      this.transictionOrderDesc = false,
      this.transactionType = "both";
      this.transactionCategory = 0;

      const date = new Date(Date.now()), y = date.getFullYear(), m = date.getMonth();
      const firstDay = new Date(y, m, 1);
      const firstDayMonth = (firstDay.getMonth() + 1).toString();
      const firstDayDayNumber = firstDay.getDate().toString();
      const lastDay = new Date(y, m + 1, 1);
      const lastDayMonth = (lastDay.getMonth() + 1).toString();
      const lastDayDayNumber = lastDay.getDate().toString();
      this.transactionStartDate = firstDay.getFullYear() + "-" + (firstDayMonth.length == 1 ? "0" : "") + firstDayMonth + "-" + (firstDayDayNumber.length == 1 ? "0" : "") + firstDayDayNumber;
      this.transactionEndDate = lastDay.getFullYear() + "-" + (lastDayMonth.length == 1 ? "0" : "") + lastDayMonth + "-" + (lastDayDayNumber.length == 1 ? "0" : "") + lastDayDayNumber;
    },
    closeModal() {
      const modalEl = document.getElementById(this.id);
      modalEl.close();
      this.setDefaultValues();
    },
    applyFilter() {
      const filterParams = {
        orderBy: this.transactionOrderBy,
        desc: this.transictionOrderDesc,
        transactionType: this.transactionType,
        categoryId: this.transactionCategory,
        startDate: this.transactionStartDate,
        endDate: this.transactionEndDate
      };
      this.$emit("applyFilter", filterParams);
      this.closeModal();
    },
  },
  mounted() {
    this.setDefaultValues();
  },
}
</script>

