<template>
  <div>
    <div class="create-category-container" @click="openModal" v-if="docId == 0 && false">
      <div class="create-category-btn"></div>
      Add new Category
    </div>
    <div class="create-category-btn" @click="openModal" v-else-if="docId == 0"></div>

    <div class="edit-btn" @click="openModal" v-else></div>
    <dialog class="modal-container" :id="docId + '-modal'">
      <div class="close-btn-container">
        <div class="close-btn" @click="closeModal"></div>
      </div>
      <div class="input-container">
        <label for="ba-name">Name:</label>
        <input id="ba-name" type="text" v-model="categoryName" class="form-control">
      </div>
      <div class="input-container">
        <label for="t-filter-type">Type:</label>
        <select id="t-filter-type" class="form-control" v-model="transactionType">
          <option value="income">Incomes</option>
          <option value="expense">Expenses</option>
        </select>
      </div>
      <div class="create-btn" :class="[allowCreation ? '' : 'btn-disabled']" @click="allowCreation ? createEditCategory : ''">Create</div>
    </dialog>
  </div>
</template>

<script>
import { doc, setDoc, updateDoc, collection } from "firebase/firestore"; 
import { mapGetters } from 'vuex';

export default {
  name: 'CategoriesEdit',
  props: ["docId", "name"],
  data() {
    return {
      categoryName: null,
      transactionType: "expense",
    }
  },
  computed: {
    ...mapGetters(["db", "uid"]),
    allowCreation() {
      return (this.categoryName != null && this.categoryName != "" && this.categoryName.trim() != "" && this.categoryName.trim() == this.categoryName);
    }
  },
  methods: {
    openModal() {
      this.categoryName = this.name;
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.showModal();
    },
    closeModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.close();
    },
    async createEditCategory() {
      if(this.docId == 0) {
        const newId = this.uid + Date.now().toString();
        await setDoc(doc(this.db, "categories", newId), {
          name: this.categoryName,
          transactionType: this.transactionType,
        }).then(_ => {
          this.$emit("reloadCategoriesList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      } else {
        const bankCategorRef = doc(this.db, "categories", this.docId);
        await updateDoc(bankCategorRef, {
          name: this.categoryName,
          transactionType: this.transactionType,
        }).then(_ => {
          this.$emit("reloadCategoriesList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      }
    },
  },
}
</script>

