<template>
  <div>
    <div class="transaction-bank-account-title">
      {{ bankAccountData.name }} <div class="transaction-bank-account-subtitle">({{ balanceOnceFiltered }}€)</div>
    </div>
    <div class="create-transaction-container">
      <TransactionOptions display="text" docId="options" @filterModal="openFilterModal" />
      <TransactionCreateEdit display="create" :docId="0" :elData="''" @reloadTransactionsList="getTransactionsList" />
    </div>

    <TransactionFilter id="filterModal" @applyFilter="applyFilter" />

    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(filteredTransactionList)" :key="k" class="bank-account-element">
        <div class="transaction-title">
          {{ filteredTransactionList[k].name }}
          <TransactionCreateEdit display="edit" :docId="k" :elData="filteredTransactionList[k]" @reloadTransactionsList="getTransactionsList" />
        </div>
        <div class="transaction-data-element">
          <div :class="[Number(filteredTransactionList[k].amount) < 0 ? 'red-text' : 'green-text']">
            {{ filteredTransactionList[k].amount }}€
          </div>
          |
          <div>
            {{ categoriesList[filteredTransactionList[k].categoryId].name }}
          </div>
          |
          <div>
            {{ filteredTransactionList[k].date }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TransactionCreateEdit from "@/components/TransactionCreateEdit.vue";
import TransactionOptions from "@/components/TransactionOptions.vue";
import TransactionFilter from "@/components/TransactionFilter.vue";
import { mapGetters } from 'vuex';
import { collection, where, query, getDocs } from "firebase/firestore";

export default {
  name: 'TransactionList',
  components: {
    TransactionCreateEdit,
    TransactionOptions,
    TransactionFilter,
  },
  data() {
    return {
      transactionsList: {},
      filterParams: null,
    }
  },
  computed: {
    ...mapGetters(["db", "bankAccountId", "bankAccountData", "categoriesList"]),
    filteredTransactionList() {
      this.transactionsList
      let result = Object.values(this.transactionsList);
      if(this.filterParams == null) {
        const date = new Date(Date.now()), y = date.getFullYear(), m = date.getMonth();
        const firstDay = new Date(y, m, 1);
        const lastDay = new Date(y, m + 1, 1);
        result = result.filter(x => Number(new Date(x.date)) >= Number(firstDay) && new Number(new Date(x.date)) < Number(lastDay));
        result.sort((a,b) => {
          return (Number(new Date(a.date)) - Number(new Date(b.date))) * -1;
        })
        return result;
      }
      
      result = result.filter(x => {
        return ( (this.filterParams.categoryId == 0 && ( this.filterParams.transactionType == "both" || 
        (this.filterParams.transactionType == "income" && x.amount > 0) || 
        (this.filterParams.transactionType == "expense" && x.amount < 0) ) ) || 
        (x.categoryId == this.filterParams.categoryId) ) &&
        x.date >= this.filterParams.startDate &&
        x.date < this.filterParams.endDate
      });

      if(this.filterParams.orderBy == "date") {
        result.sort((a,b) => {
          return (Number(new Date(a.date)) - Number(new Date(b.date))) * (this.filterParams.desc ? 1 : -1);
        });
      } else if(this.filterParams.orderBy == "amount") {
        result.sort((a,b) => {
          return (a.amount - b.amount) * (this.filterParams.desc ? 1 : -1);
        });
      }

      return result;
    },
    balanceOnceFiltered() {
      return Object.values(this.filteredTransactionList).map(x => x.amount).reduce((r, x) => {
        return r + x
      }, 0);
    }
  },
  methods: {
    async getTransactionsList() {
      const q = query(collection(this.db, "transactions"), where("bankAccountId", "==", this.bankAccountId));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.transactionsList[doc.id] = doc.data();
      });
    },
    openFilterModal() {
      const filterModalEl = document.querySelector("#filterModal");
      filterModalEl.showModal();
    },
    applyFilter(filterParams) {
      this.filterParams = filterParams;
    }
  },
  mounted() {
    this.getTransactionsList();
  },
}
</script>

