<template>
  <div>
    <div class="create-transaction-container" @click="openModal" v-if="display == 'create'">
      <div class="create-transaction-btn"></div>
      Add Transaction
    </div>
    <div class="edit-btn" @click="openModal" v-else-if="display == 'edit'"></div>
    <dialog class="modal-container" :id="docId + '-modal'">
      <div class="close-btn-container">
        <div class="close-btn" @click="closeModal"></div>
      </div>
      <div class="input-container">
        <label for="transaction-name">Name:</label>
        <input id="transaction-name" type="text" v-model="transactionName" class="form-control">
      </div>
      <div class="input-container">
        <label for="transaction-amount">Amount:</label>
        <div class="amount-input-container">
          <div class="amount-btn-container" :class="[negativeAmount ? 'negative-amount-btn-active' : 'positive-amount-btn-active']" @click="changeAmountSign">
            <div class="amount-btn negative-amount-btn" :class="[negativeAmount ? '' : '']"></div>
            <div class="amount-btn positive-amount-btn" :class="[negativeAmount ? 'not-visible' : '']"></div>
          </div>
          <input id="transaction-amount" type="number" v-model="transactionAmount" class="form-control"  :class="[negativeAmount ? 'text-red' : 'text-green']" style="width: calc(100% - 40px) !important">
        </div>
      </div>
      <div class="input-container">
        <label for="transaction-category">Category:</label>
        <select id="transaction-category" v-model="transactionCategory" class="form-control text-center">
          <option :value="0" v-if="transactionCategory == 0">--- Select an option ---</option>
          <option v-for="k in Object.keys(filteredCategoriesList)" :key="k" :value="k">{{ filteredCategoriesList[k].name }}</option>
        </select>
      </div>
      <div class="input-container">
        <label for="transaction-date">Date:</label>
        <input id="transaction-date" type="date" v-model="transactionDate" class="form-control">
      </div>
      <div class="create-btn" :class="[allowCreation ? '' : 'btn-disabled']" @click="allowCreation ? createEditTransaction : ''">Create</div>
    </dialog>
  </div>
</template>

<script>
import { doc, getDoc, setDoc, updateDoc } from "firebase/firestore"; 
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'TransactionsEdit',
  props: ["display", "docId", "elData"],
  data() {
    return {
      transactionName: null,
      transactionAmount: null,
      transactionCategory: null,
      transactionDate: null,

      negativeAmount: true,
    }
  },
  computed: {
    ...mapGetters(["db", "uid", "bankAccountId", "bankAccountData", "categoriesList"]),
    filteredCategoriesList() {
      const result = {};
      const transactionType = this.negativeAmount ? "expense" : "income";
      Object.keys(this.categoriesList).forEach(x => {
        if(this.categoriesList[x].transactionType == transactionType) {
          result[x] = this.categoriesList[x];
        }
      });
      return result;
    },
    allowCreation() {
      return (this.transactionName != null && this.transactionName != "" && this.transactionName.trim() != "" && this.transactionName.trim() == this.transactionName) &&
      (this.transactionAmount != null && typeof this.transactionAmount != "string") &&
      (this.transactionCategory != null && this.transactionCategory != 0 && this.transactionCategory != "") &&
      (this.transactionDate != null && this.transactionDate != "" && typeof this.transactionDate == "string");
    }
  },
  methods: {
    ...mapMutations(["setBankAccountData"]),
    openModal() {
      this.transactionName = this.elData.name;
      this.transactionAmount = this.elData.amount;
      this.transactionCategory = this.elData.categoryId ? this.elData.categoryId : 0;
      this.transactionDate = this.elData.date;
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.showModal();
    },
    closeModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.close();
    },
    changeAmountSign() {
      this.negativeAmount = !this.negativeAmount;
      this.transactionCategory = 0;
    },
    async updateBankAccountBalance() {
      const docRef = doc(this.db, "bankAccounts", this.bankAccountId);
      const docSnap = await getDoc(docRef);
      
      if (docSnap.exists()) {
        const updatedBalance = Number(docSnap.data().balance) + (Number(this.transactionAmount) * (this.negativeAmount ? -1 : 1));
        updateDoc(docRef, {
          balance: updatedBalance,
        }).then(_ => {
          const newBankAccountData = JSON.parse(JSON.stringify(this.bankAccountData));
          newBankAccountData.balance = updatedBalance;
          this.setBankAccountData(newBankAccountData);
        })
      } else {
        // docSnap.data() will be undefined in this case
        console.log("Error on getting bankAccount current balance.");
      }
    },
    async createEditTransaction() {
      if(this.docId == 0) {
        const newId = this.uid + Date.now().toString();
        await setDoc(doc(this.db, "transactions", newId), {
          name: this.transactionName,
          amount: Number(this.transactionAmount) * (this.negativeAmount ? -1 : 1),
          categoryId: this.transactionCategory,
          bankAccountId: this.bankAccountId,
          date: this.transactionDate,
        }).then(_ => {
          this.updateBankAccountBalance();
          this.$emit("reloadTransactionsList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      } else {
        const bankAccountRef = doc(this.db, "transactions", this.docId);
        await updateDoc(bankAccountRef, {
          name: this.transactionName,
          amount: Number(this.transactionAmount) * (this.negativeAmount ? -1 : 1),
          categoryId: this.transactionCategory,
          date: this.transactionDate,
        }).then(_ => {
          this.updateBankAccountBalance();
          this.$emit("reloadTransactionsList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      }
    },
  },
}
</script>

