<template>
  <div>
    <div class="options-btn-container" @click="openModal" v-if="display == 'text'">
      <div class="options-btn"></div>
      Options
    </div>
    <div class="options-btn" @click="openModal" v-else-if="display == 'icon'"></div>
    <dialog class="modal-container" :id="docId + '-modal'">
      <div class="close-btn-container">
        <div class="close-btn" @click="closeModal"></div>
      </div>
      <div class="modal-subtitle">Options:</div>
      <div class="btns-container">
        <div class="option-btn-container" @click="openFilterModal">
          <div class="filter-btn"></div>Filter
        </div>
        <div class="option-btn-container" @click="openChartModal">
          <div class="chart-btn"></div>Charts
        </div>
      </div>
    </dialog>
  </div>
</template>

<script>

export default {
  name: 'TransactionOptions',
  props: ["display", "docId"],
  data() {
    return {
    }
  },
  methods: {
    openModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.showModal();
    },
    closeModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.close();
    },
    openFilterModal() {
      this.closeModal();
      this.$emit("filterModal");
    },
    openChartModal() {
      this.closeModal();
      this.$emit("chartModal");
    },
  },
}
</script>

